import * as React from 'react';
import Item from './Item';
import ItemService from './Services/ItemsService';

interface IViewProps {
  name: string
  items: string[]
}

class Section extends React.Component<IViewProps, any> {
  public renderInvisibleImages() {
    const n = (5 - this.props.items.length % 5) % 5
    return [...Array.from(Array(n).keys())].map(_ => <img
      key={`${this.props.name}-${_}`}
      className="item-image item-image-empty"
      src="/transparent.png"
    />);
  }

  public render() {
    return (
      <div className="column is-half">
        <div className="items-section">
          <h1 className="is-size-5 has-text-weight-semibold title">{this.props.name} ({ItemService.count(this.props.items)}/{this.props.items.length})</h1>
          {(this.props.items as string[]).map(item => <Item hash={item} key={item} />)}
          {this.renderInvisibleImages()}
        </div>
      </div>
    );
  }
}

export default Section;



