import * as React from 'react';
import slugify from 'slugify';
import { AppContext } from './App';
import Section from './Section';

interface ISectionProps {
  name: string
  sections: any[]
}

class Page extends React.Component<ISectionProps, any> {
  public render() {
    return (
    <AppContext.Consumer>
      {(app) => {
        if (slugify(this.props.name) !== app.state.currentPage) {
          return;
        }
        return (
          <div className="columns is-multiline">
            {this.props.sections.map(section => 
              <Section key={`S-${this.props.name}-V-${section.name}`} name={section.name} items={section.items} />
            )}
          </div>
        );
      }}
    </AppContext.Consumer>
    );
  }
}

export default Page;



