import * as queryString from 'query-string';
export const clientID = process.env.NODE_ENV === 'development' ? 23684 : 23709;

export default class OAuthService {
  public generateLoginUrl(): string {
    return `https://www.bungie.net/en/OAuth/Authorize?client_id=${clientID}&response_type=code`;
  }
  public async fetchToken(code: string) {
    return fetch('https://www.bungie.net/platform/app/oauth/token/', {
      body: queryString.stringify({
        client_id: clientID,
        code: `${code}`,
        grant_type: 'authorization_code',
      }),
      headers: {
        'content-type': 'application/x-www-form-urlencoded',
        // 'user-agent': this.userAgent,
      },
      method: 'POST'
    })
      .then(response => response.json())
  }
}


