export interface IStringArrayMap { 
  [s: string]: string[];
}

export interface IBooleanMap {
  [s: string]: boolean
}

class ItemsService {
  private db: any
  private equivalenceGroups: IStringArrayMap
  private userItems: IBooleanMap
  private sockets: any
  private missingItems: {
    armor: string[],
    weapons: string[]
  }
  
  constructor() {
    this.db = {}
    this.equivalenceGroups = {}
    this.userItems = {}
    this.sockets = {}
    this.missingItems = {
      armor: [],
      weapons: [],
    }
    this.setUserItem = this.setUserItem.bind(this);
  }

  public setDB(db: any) {
    this.db = db;
  }

  public setEquivalenceGroups(groups: string[][]) {
    groups.forEach(group => group.forEach(hash => this.equivalenceGroups[hash] = group));
  }

  public has(hash: string) {
    const group = this.equivalenceGroups[hash] ? this.equivalenceGroups[hash] : [hash];
    return group.some(itemHash => this.userItems[itemHash]);
  }

  public find(hash: string) {
    return this.has(hash) ? this.db[hash] : null;
  }

  public name(hash: string) {
    return this.db[hash].displayProperties.name;
  }

  public imageURL(hash: string) {
    return `https://www.bungie.net${this.db[hash].displayProperties.icon}`;
  }

  public missingWeapons(): string[] {
    return this.missingItems.weapons;
  }

  public missingArmor(): string[] {
    return this.missingItems.armor;
  }

  public setUserItems(data: any) {
    this.sockets = data.itemComponents.sockets.data;

    [data.characterEquipment.data, data.characterInventories.data]
      .forEach(v => Object.keys(v).map(key => v[key].items)
        .forEach(items => items.forEach(this.setUserItem)));

    data.profileInventory.data.items
      .forEach(this.setUserItem);
  }

  public count(items: string[]) {
    return items.reduce((acc, item) => this.has(item) ? acc + 1 : acc, 0);
  }

  public computeMissingItems(pages: any[]): void {
    const allSectionItems: IBooleanMap = {};
    pages.forEach(page => 
      page.sections.forEach((section: any) => 
        section.items.forEach((hash: string) => allSectionItems[hash] = true)
      )
    );

    for (const hash of Object.keys(this.db)) {
      if (allSectionItems[hash]) {
        continue;
      }

      const item = this.db[hash];
      /*
        Info:
        - https://bungie-net.github.io/multi/schema_Destiny-TierType.html
        - https://bungie-net.github.io/multi/schema_Destiny-DestinyItemType.html
        - https://bungie-net.github.io/multi/schema_Destiny-DestinyItemSubType.html
      */
      if (item && item.inventory && item.inventory.tierType === 5) {
        // legendary item
        if (item.itemType === 3) {
          this.missingItems.weapons.push(hash);
        } else if (item.itemType === 2) {
          this.missingItems.armor.push(hash);
        }
      }
    }
  }

  private setUserItem(item: any) {
    this.userItems[item.itemHash] = true;

    if (this.sockets[item.itemInstanceId]) {
      const sockets = this.sockets[item.itemInstanceId];
      (sockets.sockets || []).forEach((socket: any) => {
        const plugs = [socket, ...(socket.reusablePlugs || [])];
        plugs.forEach(plug => {
          if (plug.plugObjectives) {
            if (plug.plugObjectives.every((objective: any) => objective.complete)) {
              const plugHash = plug.plugHash || plug.plugItemHash;
              this.userItems[plugHash] = true;
            }
          }
        })
      })
    }
  }
}

const ItemServiceInstance = new ItemsService();

export default ItemServiceInstance;


