const APIKEY = process.env.NODE_ENV === 'development' ? '655ae4bfc0f24f69a0671761b3ea818c' : 'c2f98e51c6c04e12b1b1d8fd3277de44';

export default class DestinyService {
  private token: string
  private membershipId?: string
  private membershipType?: string
  constructor(token: string) {
    this.token = token;
  }

  public async configure(membership: string) {
    const response = await fetch(`https://www.bungie.net/Platform/User/GetMembershipsById/${membership}/254/`, {
      headers: {
        authorization: `Bearer ${this.token}`,
        'x-api-key': APIKEY
      }
    }).then(res => res.json());
    this.membershipId = response.Response.destinyMemberships[0].membershipId
    this.membershipType = response.Response.destinyMemberships[0].membershipType
  }

  public fetchInventory() {
    return fetch(`https://www.bungie.net/Platform/Destiny2/${this.membershipType}/Profile/${this.membershipId}/?components=102,201,205,300,301,302,303,304,305,306,307,308`, {
      headers: {
        authorization: `Bearer ${this.token}`,
        'x-api-key': APIKEY
      }
    }).then(response => response.json());
  }
}


