import * as React from 'react';
import slugify from 'slugify';
import { AppContext } from './App';
import ItemServiceInstance from './Services/ItemsService';

class Navigation extends React.Component<any, any> {
  public render() {
    return (
      <AppContext.Consumer>
        {(app) => (
          <div className="navigation">
            {app.state.pages.map((page: any, index) => (
              <button key={`nav-${index}`} onClick={app.setPage.bind(app, slugify(page.name))}>
                {page.name} ({this.countCollected(page)}/{this.countItems(page)})
              </button>
            ))}
            <button onClick={app.setPage.bind(app, slugify('Missing Items'))}>
              Missing Items
            </button>
          </div>
        )}
      </AppContext.Consumer>
    )
  }

  private countCollected(page: any) {
    return (page.sections as any[]).reduce((acc, section) => acc + ItemServiceInstance.count(section.items), 0);
  }

  private countItems(page: any) {
    return (page.sections as any[]).reduce((acc, section) => acc + section.items.length, 0);
  }
}

export default Navigation;



