import * as queryString from 'query-string';
import * as React from 'react';
import slugify from 'slugify';
import './App.css';
import Navigation from './Navigation';
import Page from './Page';
import DestinyService from './Services/DestinyService';
import ItemService from './Services/ItemsService';
import OAuthService from './Services/OAuthService';

export const AppContext = React.createContext({
  setPage: (slug: string): void => {return;},
  state: {
    currentPage: '',
    pages: [],
  },
});

interface IAppState {
  message: string
  pages: any
  currentPage: string
}

class App extends React.Component<any, IAppState> {
  private oauthService: OAuthService
  private destiny: DestinyService

  constructor(props: any) {
    super(props);
    this.oauthService = new OAuthService();
    this.state = {
      currentPage: '',
      message: '',
      pages: [],
    }
    this.setPage = this.setPage.bind(this);
  }
  public async componentWillMount() {
    const queryParams = queryString.parse(location.search);
    ItemService.setDB(await fetch('/db.json').then(r => r.json()));
    ItemService.setEquivalenceGroups(await fetch('/equivalentItems.json').then(r => r.json()));
    const pagesJson = await fetch('/sections.json').then(r => r.json());
    pagesJson.pages.forEach((page: any) => {
      page.resolvedSections = [];
      page.sections.forEach((sectionKey: any) => page.resolvedSections.push(pagesJson.sections[sectionKey]));
      page.sections = page.resolvedSections;
    });
    await this.setState({ pages: pagesJson.pages });
    ItemService.computeMissingItems(this.state.pages);
    
    if (window.location.hash) {
      this.setPage(window.location.hash.substr(1));
    } else {
      this.setPage(slugify(this.state.pages[0].name));
    }
    if (queryParams.code) {
      this.setState({ message: 'Generating OAuth access token...' });
      window.history.replaceState({}, document.title, location.pathname);
      const oauthResponse = await this.oauthService.fetchToken(queryParams.code);
      
      this.setState({ message: 'Fetching your profile from Bungie...' });
      this.destiny = new DestinyService(oauthResponse.access_token)
      await this.destiny.configure(oauthResponse.membership_id);

      this.setState({ message: 'Fetching your Destiny 2 items...' });
      const inventoryResponse = await this.destiny.fetchInventory();
      this.setState({ message: 'Processing your Destiny 2 items...' });      
      ItemService.setUserItems(inventoryResponse.Response);
        
      this.setState({ message: '' });
    }
  }

  public setPage(slug: string) {
    window.location.hash = slug;
    return this.setState({ currentPage: slug });
  }

  public render() {
    return (
      <AppContext.Provider value={this}>
        <div className="App">
          <Navigation />
          {
            this.state.message ? 
            this.state.message :
            <button><a href={this.oauthService.generateLoginUrl()}>Login here!</a></button>
          }
          {
            this.state.pages.map((page: any) => {
              return <Page key={`S-${page.name}`} name={page.name} sections={page.sections} />;
            })
          }
          <Page name="Missing Items" sections={[
            { name: "Missing Weapons", items: ItemService.missingWeapons() },
            { name: "Missing Armor", items: ItemService.missingArmor() },
          ]} />
        </div>
      </AppContext.Provider>
    );
  }
}

export default App;



